@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html,body{height:100%}body{margin:0;font-family:'Valera Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:var(--global-background-color)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{z-index:-101;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100%;font-size:var(--global-font-size);letter-spacing:-1px;font-weight:700}h1,h2,h3,h4,h5,h6{padding:0;margin:0}


.section{background:var(--global-color__white);border:var(--global-border-thickness) solid var(--global-color__black);border-radius:var(--global-border-radius);padding:var(--global-padding-lg)}.section+.section{margin-top:var(--global-margin)}

.dropdown{background:var(--global-color__white);border-radius:var(--global-border-radius);border:var(--global-border-thickness) solid var(--global-color__black);padding:var(--global-padding);font-size:var(--global-font-size-sm);font-weight:700;cursor:pointer}


.masthead{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:0 var(--global-padding);border-bottom:var(--global-border-thickness) solid var(--global-color__black);min-height:var(--global-y)}

.page{--page-padding: var(--global-y);padding:var(--page-padding)}@media only screen and (max-width: 564px){.page{--page-padding: 32px}}.page:before{content:"";background-image:url(/sea-bass-ui/static/media/fish.8fae55fe.png);background-size:cover;position:fixed;top:var(--global-y);right:0px;bottom:0px;left:50%;opacity:0.05;z-index:-100}

.button{background:var(--global-color__white);border-radius:var(--global-border-radius);border:var(--global-border-thickness) solid var(--global-color__black);padding:var(--global-padding);font-size:var(--global-font-size-sm);font-weight:700;cursor:pointer;position:relative;top:0;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)}.button:hover{top:-1px}.button:focus,.button:active{border-radius:10px}

.go{border-bottom:0.05em solid var(--global-color__blue-300);box-shadow:inset 0 -0.05em 0 var(--global-color__blue-300);color:inherit;transition:all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);text-decoration:none}.go:hover,.go:focus,.go:active{background-color:var(--global-color__blue-300)}.go.go-isPlain{border:0;box-shadow:none;color:none;position:relative;bottom:0;background-color:transparent}.go.go-isPlain:hover,.go.go-isPlain:focus,.go.go-isPlain:active{bottom:2px;background-color:transparent}

.heading{line-height:var(--heading-lineHeight);font-size:var(--global-font-size)}.heading-weight--light{font-weight:300}.heading-weight--normal{font-weight:500}.heading-weight--bold{font-weight:700}

.currency{display:-webkit-inline-flex;display:inline-flex;padding:8px;border-radius:var(--global-border-radius)}.currency sub{margin-left:4px;font-size:12px;margin-top:auto;margin-bottom:4px}.currency-USD{background:var(--global-color__green-200);color:var(--global-color__green-100);border:1px solid var(--global-color__green-100)}.currency-ETH{background:var(--global-color__red-200);color:var(--global-color__red-100);border:1px solid var(--global-color__red-100)}.currency-LTC{background:var(--global-color__blue-200);color:var(--global-color__blue-100);border:1px solid var(--global-color__blue-100)}.currency-BTC{background:var(--global-color__yellow-200);color:var(--global-color__yellow-100);border:1px solid var(--global-color__yellow-100)}

.error{background-color:var(--global-color__white)}

@-webkit-keyframes rotating{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotating{100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading{border-radius:50%;width:50px;height:50px;border:6px solid var(--global-background-color);border-top:6px solid var(--global-color__black);-webkit-animation:rotating 1.2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);animation:rotating 1.2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);margin-left:auto;margin-right:auto}

.description{margin:var(--global-margin-md) 0}.description dt{font-weight:var(--global-font-weight-light);font-size:var(--global-font-size-sm);color:var(--global-font-color-light);letter-spacing:normal;margin-bottom:var(--global-font-size-sm)}.description dd{margin:0;margin-left:var(--global-margin-md);line-height:32px}.description+.description{margin-top:var(--global-margin-xl)}

.card{background:var(--global-color__white);border:var(--global-border-thickness) solid var(--global-color__black);border-radius:var(--global-border-radius)}.card-header{border-bottom:var(--global-border-thickness) solid var(--global-color__black);padding:var(--global-padding-lg)}.card-body{padding:var(--global-padding-lg)}

.brand{--brand-modifier: 25px;--brand-md: 100px;--brand-lg: calc(var(--brand-md) + var(--brand-modifier));--brand-xl: calc(var(--brand-lg) + var(--brand-modifier));--brand-sm: calc(var(--brand-md) - var(--brand-modifier));--brand-xs: calc(var(--brand-md) - var(--brand-modifier))}.brand-xs{height:var(--brand-xs)}.brand-sm{height:var(--brand-sm)}.brand-md{height:var(--brand-md)}.brand-lg{height:var(--brand-lg)}.brand-xl{height:var(--brand-xl)}

.page-landing{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;height:100%}.page-landing .landing-form{margin-top:calc(var(--global-y) * -1);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.page-landing .landing-form *+*{margin-top:var(--global-margin)}.drunk-button{font-size:var(--global-font-size-xs);letter-spacing:normal;margin-top:var(--global-margin)}

.page-find{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.page-find .section{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.page-find .section .heading{-webkit-align-self:center;align-self:center}.page-find .section-results{-webkit-align-items:center;align-items:center;padding:var(--global-padding-lg);text-align:center}.page-find .section-results *+*{margin-top:var(--global-margin)}

.cocktails-list-item{margin-top:12px}

.page-make{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.card-header-img{width:200px;height:auto}


:root{--global-color__black: #242423;--global-color__black-200: #cecece;--global-color__white: #fafafa;--global-color__green-100: #a5d651;--global-color__green-200: #f1fcd3;--global-color__red-100: #f95657;--global-color__red-200: #ffdfe3;--global-color__blue-100: #4dbcfb;--global-color__blue-200: #e0f4ff;--global-color__blue-300: #b4e7f8;--global-color__yellow-100: #ecb417;--global-color__yellow-200: #fff8dd;--global-font-size: 1.5rem;--global-font-size-sm: 1rem;--global-font-size-xs: .75rem;--global-font-size-md: var(--global-font-size);--global-font-size-lg: 2rem;--global-font-weight: 700;--global-font-weight-light: 300;--global-font-weight-normal: 500;--global-font-weight-bold: 700;--global-font-color: var(--global-color__black);--global-font-color-light: rgb(56, 56, 56);--global-background-color: #fdf1db;--global-border-thickness: 3px;--global-border-radius: 5px;--global-padding: 12px;--global-padding-sm: 8px;--global-padding-md: var(--global-padding);--global-padding-lg: 16px;--global-padding-xl: 24px;--global-margin: 16px;--global-margin-xs: 8px;--global-margin-sm: 12px;--global-margin-md: var(--global-margin);--global-margin-lg: 24px;--global-margin-xl: 32px;--global-y: 100px}

